#elements-list-1,
#elements-list-2,
#elements-list-3,
#elements-list-4,
#elements-list-5 {
  list-style: none;
  padding: 0;
  margin: 0;
  margin-top: 30px;

  .element {
    display: flex;
    align-items: center;
    gap: 15px;
    position: relative;
    padding: 15px 0;

    .element-number {
      color: $accent-color;
      font-family: $psytype;
      font-size: 55px;
      line-height: 1;
      letter-spacing: 0.55px;
      text-transform: uppercase;
      width: 71px;
      height: 71px;
      border-radius: 50%;
      border: 1px solid $accent-color;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-shrink: 0;

      @include responsive(mobile, tablet) {
        font-size: 32px;
        letter-spacing: 0.32px;
        width: 51px;
        height: 51px;
      }
    }
  }
}
